/* Metrics */
.veui-checkbox {
  display: inline-block;
  margin-right: 20px;
  color: #333;
  font-size: 14px;
  line-height: 1;
}
.veui-checkbox input,
.veui-checkbox .veui-icon {
  display: none;
}
.veui-checkbox span {
  vertical-align: middle;
  cursor: pointer;
}
.veui-checkbox-box {
  display: inline-block;
  position: relative;
  background-color: #fff;
  border-radius: 3px;
  height: 16px;
  width: 16px;
  margin-right: 7px;
  font-size: 16px;
  border: 1px solid #dbdbdb;
  box-shadow: inset 0 1px 2px 0 rgba(0, 0, 0, 0.1);
}
.veui-checkbox:hover .veui-checkbox-box {
  border-color: #3685ff;
}
.veui-checkbox :checked + .veui-checkbox-box,
.veui-checkbox :indeterminate + .veui-checkbox-box {
  background-color: #3685ff;
  border-color: #3685ff;
}
.veui-checkbox :checked + .veui-checkbox-box > .veui-icon,
.veui-checkbox :indeterminate + .veui-checkbox-box > .veui-icon {
  display: inline-block;
  position: absolute;
  color: #fff;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0.75);
          transform: translate(-50%, -50%) scale(0.75);
}
.veui-checkbox :disabled,
.veui-checkbox :disabled ~ span {
  cursor: not-allowed;
}
.veui-checkbox :disabled ~ span {
  color: #999;
}
.veui-checkbox :disabled + .veui-checkbox-box,
.veui-checkbox :indeterminate:disabled + .veui-checkbox-box {
  border-color: #dbdbdb;
  background-color: #e7e7e7;
}
.veui-checkbox :disabled + .veui-checkbox-box .veui-icon,
.veui-checkbox :indeterminate:disabled + .veui-checkbox-box .veui-icon {
  color: #999;
}
.veui-checkbox :checked + .veui-checkbox-box,
.veui-checkbox :disabled + .veui-checkbox-box,
.veui-checkbox :indeterminate + .veui-checkbox-box {
  box-shadow: none;
}
.veui-checkbox[ui~="small"] {
  font-size: 12px;
  line-height: 1;
}
.veui-checkbox[ui~="small"] .veui-checkbox-box {
  margin-right: 6px;
  height: 12px;
  width: 12px;
  font-size: 12px;
}
.veui-checkbox[ui~="small"] .veui-checkbox-box + span {
  font-size: 12px;
}



