.veui-uploader {
  width: 292px;
  overflow: hidden;
}
.veui-uploader-button-container {
  position: relative;
  margin-bottom: 20px;
}
.veui-uploader-input-label {
  cursor: pointer;
  display: inline-block;
  color: #333;
  margin-right: 1em;
  box-sizing: border-box;
}
.veui-uploader-input-label-icon {
  margin-right: 5px;
}
.veui-uploader-input-label-disabled {
  cursor: default !important;
  border: none !important;
  background-color: #f1f1f1 !important;
  color: #999 !important;
  box-shadow: none !important;
}
.veui-uploader-input-label-disabled:hover {
  border: none !important;
  background-color: #f1f1f1 !important;
  color: #999 !important;
  box-shadow: none !important;
}
.veui-uploader-input-label-image {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.veui-uploader-input-label-image::before,
.veui-uploader-input-label-image::after {
  content: '';
  position: absolute;
}
.veui-uploader-input-label-image::before {
  border-top: 4px solid #dbdbdb;
  top: calc(50% - 2px);
  left: 37px;
  width: 60px;
}
.veui-uploader-input-label-image::after {
  border-left: 4px solid #dbdbdb;
  top: 37px;
  left: calc(50% - 2px);
  height: 60px;
}
.veui-uploader-list-file,
.veui-uploader-list-image {
  padding: 0;
  margin: 0;
  list-style: none;
  overflow: hidden;
}
.veui-uploader-list-file img,
.veui-uploader-list-image img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle;
}
.veui-uploader-list-file-status,
.veui-uploader-list-image-status {
  line-height: 1.2;
}
.veui-uploader-list-file-status > span,
.veui-uploader-list-image-status > span {
  display: inline-block;
  vertical-align: middle;
  word-break: break-all;
}
.veui-uploader-list-file-status::after,
.veui-uploader-list-image-status::after {
  content: '';
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}
.veui-uploader-list-file-success,
.veui-uploader-list-image-success {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 9px 4px;
  display: block;
  background-color: #fff;
  color: #3faca3;
}
.veui-uploader-list-file {
  color: #3685ff;
}
.veui-uploader-list-file li {
  position: relative;
  padding: 9px 4px;
  box-sizing: content-box;
}
.veui-uploader-list-file li:hover {
  background-color: #f1f1f1;
}
.veui-uploader-list-file li:hover .veui-button[ui~="link"] {
  background-color: #f1f1f1;
}
.veui-uploader-list-file li:hover .veui-button[ui~="delete"] {
  visibility: visible;
}
.veui-uploader-list-file-name,
.veui-uploader-list-file-size {
  display: inline-block;
  vertical-align: middle;
}
.veui-uploader-list-file-name {
  overflow: hidden;
  width: 70%;
}
.veui-uploader-list-file-size {
  color: #999;
  width: calc(30% - 2em);
}
.veui-uploader-list-file-status {
  display: inline-block;
  width: calc(100% - 3em);
  height: 100%;
}
.veui-uploader-list-file-success svg {
  position: relative;
  top: 2px;
}
.veui-uploader-list-file .veui-uploader-progress {
  width: calc(100% - 3em);
  top: 0;
}
.veui-uploader-list-file .veui-uploader-progress + button {
  float: right;
  position: relative;
  right: 5px;
}
.veui-uploader-list-icon {
  display: none;
}
.veui-uploader-list-preview {
  height: 32px;
  padding: 4px !important;
}
.veui-uploader-list-preview-container {
  display: inline-block;
  vertical-align: middle;
  width: 32px;
  height: 32px;
  line-height: 32px;
  font-size: 0;
  text-align: center;
  background-color: #fff;
  margin-right: 8px;
}
.veui-uploader-list-preview .veui-uploader-list-file-name {
  width: calc(70% -  32px );
}
.veui-uploader-list-preview .veui-uploader-progress {
  width: calc(100% - 3em);
  top: 9px;
}
.veui-uploader-list-preview .veui-uploader-progress + button {
  top: 8px;
}
.veui-uploader-list-image li {
  overflow: hidden;
  width: 140px;
  height: 140px;
  padding: 2px;
  box-sizing: border-box;
  margin: 0 10px 5px 0;
  border: 1px dashed #e7e7e7;
  text-align: center;
  line-height: 134px;
  position: relative;
}
.veui-uploader-list-image li button[ui~="operation"] {
  position: absolute;
  min-width: 70px;
  font-size: 12px;
  height: 30px;
  bottom: 30px;
  left: calc(50% - 35px);
}
li:hover .veui-uploader-list-image-mask {
  display: block;
}
.veui-uploader-list-image-mask {
  display: none;
  position: absolute;
  top: 2px;
  left: 2px;
  width: calc(100% -  4px );
  height: calc(100% -  4px );
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
}
.veui-uploader-list-image-mask button {
  vertical-align: middle;
  background: none;
  color: #fff !important;
  font-size: 14px;
}
.veui-uploader-list-image-mask button:active,
.veui-uploader-list-image-mask button:hover {
  background: none;
  color: #fff !important;
}
.veui-uploader-list-image-status {
  margin-top: 10%;
  height: 40%;
}
.veui-uploader-list-image-status > span {
  max-width: 90%;
}
.veui-uploader-list-image-success {
  padding-top: 50px;
  line-height: 1;
}
.veui-uploader-list-image-success span {
  display: block;
  margin-bottom: 10px;
}
.veui-uploader-list-image-success svg {
  font-size: 16px;
}
.veui-uploader-list-image .veui-uploader-progress {
  display: block;
}
.veui-uploader-warning-container {
  margin: 20px 0 5px 5px;
  overflow: hidden;
}
.veui-uploader-tip {
  color: #999;
  margin-left: 3px;
}
.veui-uploader-uploading {
  color: #3685ff;
}
.veui-uploader-failure {
  color: #ff5b5b;
}
.veui-uploader-success {
  color: #3faca3;
}
.veui-uploader-warning {
  color: #fe9700;
}
.veui-uploader-hide {
  display: none;
}
.veui-uploader[ui~="ellipsis"] .veui-uploader-list-file-name {
  text-overflow: ellipsis;
}
.veui-uploader[ui~="multiline"] .veui-uploader-list-file-name {
  word-break: break-all;
}
.veui-uploader[ui~="horizontal"] {
  width: 100%;
}
.veui-uploader[ui~="horizontal"] .veui-uploader-list-file {
  width: 100%;
}
.veui-uploader[ui~="horizontal"] .veui-uploader-list-file li {
  float: left;
  width: 292px;
  margin-right: 10px;
}
.veui-uploader[ui~="horizontal"] .veui-uploader-list-image li {
  float: left;
}
.veui-uploader[ui~="list-icon"] .veui-uploader-list-icon {
  display: inline;
  margin-right: 3px;
}
.veui-uploader[ui~="bottom-mask"] .veui-uploader-list-image-mask {
  top: auto;
  bottom: 2px;
  left: 2px;
  width: calc(100% -  4px );
  height: 30px;
  line-height: 30px;
}
.veui-uploader .veui-button[ui~="delete"] {
  color: #ff5b5b;
  font-size: 16px;
  min-width: 16px;
  width: 16px;
  visibility: hidden;
}
.veui-uploader-warning-enter,
.veui-uploader-warning-leave-active {
  margin: 0;
  opacity: 0;
  height: 0;
  width: 0;
  line-height: 0;
}
.veui-uploader-warning-enter-active,
.veui-uploader-warning-leave-active {
  transition: all .6s ease;
}
.veui-uploader-list-enter,
.veui-uploader-list-leave-active {
  opacity: 0;
}
.veui-uploader-list-enter-active,
.veui-uploader-list-leave-active {
  transition: all .6s ease;
}
.veui-uploader-list-move {
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}
.veui-uploader-fade-leave-active {
  opacity: 0;
  transition: all .6s ease;
}
.veui-uploader-progress {
  display: inline-block;
  position: relative;
  line-height: 1.2;
  color: #3685ff;
}
.veui-uploader-progress-bar,
.veui-uploader-progress-bar-full {
  display: inline-block;
  position: absolute;
  left: calc(50% - 35px);
  top: 50%;
  height: 6px;
  max-width: 70px !important;
  border-radius: 3px;
}
.veui-uploader-progress-bar {
  background-color: #3685ff;
}
.veui-uploader-progress-bar-full {
  z-index: -1;
  width: 70px;
  background-color: #f1f1f1;
}



