html {
  font-size: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
html,
button,
input,
select,
textarea {
  font-family: Helvetica, Arial, Verdana, "PingFang SC", "STHeiti", "Microsoft YaHei", "SimHei", sans-serif;
}
body {
  margin: 0;
}
a:focus {
  outline: thin dotted;
}
a:active,
a:hover {
  outline: 0;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
h2 {
  font-size: 1.5em;
  margin: 0.83em 0;
}
h3 {
  font-size: 1.17em;
  margin: 1em 0;
}
h4 {
  font-size: 1em;
  margin: 1.33em 0;
}
h5 {
  font-size: 0.83em;
  margin: 1.67em 0;
}
h6 {
  font-size: 0.67em;
  margin: 2.33em 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
blockquote {
  margin: 1em 40px;
}
dfn {
  font-style: italic;
}
hr {
  box-sizing: content-box;
  height: 0;
}
p,
pre {
  margin: 1em 0;
}
code,
kbd,
pre,
samp {
  font-family: Monaco, Consolas, monospace;
  font-size: 1em;
}
pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}
q {
  quotes: none;
}
q:before,
q:after {
  content: "";
  content: none;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
dl,
menu,
ol,
ul {
  margin: 1em 0;
}
dd {
  margin: 0 0 0 40px;
}
menu,
ol,
ul {
  padding: 0 0 0 40px;
}
nav ul,
nav ol {
  list-style: none;
  list-style-image: none;
}
img {
  border: 0;
  -ms-interpolation-mode: bicubic;
}
svg:not(:root) {
  overflow: hidden;
}
form {
  margin: 0;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
  white-space: normal;
  *margin-left: -7px;
}
button,
input,
select,
textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
  *vertical-align: middle;
}
button,
input {
  line-height: normal;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
  *overflow: visible;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
  *height: 13px;
  *width: 13px;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden] {
  display: none;
}
mark {
  background: #ff0;
  color: #666;
}
figure {
  margin: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  font-size: 14px;
  font-weight: 400;
}
@media screen and (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 2dppx) {
html {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}
a {
  color: #3685ff;
}
a:hover {
  color: #3178e6;
}
a:active {
  color: #2b6acc;
}
input,
button,
textarea {
  font: inherit;
  font-weight: inherit;
  color: inherit;
}
#app {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
#app.console-expanded .output {
  overflow: auto;
  height: 40vh;
}
#app.console-expanded main {
  height: calc(100vh - 40vh - 30px);
}
#main-nav,
#console {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
}
#main-nav {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 240px;
  border-right: 1px solid #eee;
  font-weight: 300;
}
#main-nav h1 {
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #eee;
  margin: 0;
  padding: 0 24px;
  font-size: 18px;
}
#main-nav ul {
  height: calc(100vh - 105px);
  margin: 0;
  padding: 0;
  overflow: auto;
}
#main-nav li a {
  display: block;
  padding: .5em 24px;
  text-decoration: none;
  color: #999;
  outline: none;
  line-height: 2;
}
#main-nav .router-link-active {
  color: #3685ff;
  background-color: #f6f9ff;
  font-weight: 600;
}
#main-nav footer {
  border-top: 1px solid #eee;
  color: #999;
  font-size: 12px;
  line-height: 45px;
  padding-left: 24px;
}
main {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 240px;
  overflow: scroll;
  padding: 1em 4em 0;
  height: calc(100vh - 30px);
  transition: height .2s;
}
main h1 {
  border-bottom: 1px solid #eee;
  margin-bottom: 2em;
  padding-bottom: 1em;
  font-size: 18px;
}
#console {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 240px;
  background-color: #fff;
}
#console h2 {
  height: 30px;
  line-height: 30px;
  margin: 0;
  padding: 0 1em;
  background-color: #f6f9ff;
  font-size: 12px;
  font-weight: 600;
  color: #666;
  cursor: pointer;
}
#console h2 > * {
  margin-right: 5px;
}
#console h2 .fa-icon {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}
#console h2 small {
  margin-left: 3px;
}
#console .output {
  overflow: hidden;
  height: 0;
  margin: 0;
  line-height: 20px;
  font-size: 10px;
  transition: height .2s;
}
#console .output .log {
  position: relative;
  min-height: 20px;
  margin: 0;
  padding: 0 1em;
  border-bottom: 1px solid #f3f3f3;
}
#console .output .log .line:not(:last-child) {
  border-bottom: 1px solid #f3f3f3;
}
#console .output .log:last-child::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 2px;
  background-color: #3685ff;
}
#console small {
  color: #999;
}
.fa-icon {
  width: auto;
  height: 1em;
}



