/* Metrics */
.veui-input,
.veui-textarea {
  height: 36px;
  width: 300px;
  line-height: 1;
  vertical-align: middle;
  border: 1px solid #3685ff;
  border-radius: 2px;
  border-color: #dbdbdb;
  background-color: #fff;
  font-size: 14px;
}
.veui-input:hover,
.veui-textarea:hover {
  border-color: #3685ff;
}
.veui-input:focus,
.veui-textarea:focus {
  border-color: #3685ff;
  box-shadow: 0 0 4px rgba(54, 133, 255, 0.4);
  outline: none;
}
.veui-input[readonly],
.veui-textarea[readonly],
.veui-input:disabled,
.veui-textarea:disabled {
  background-color: #f1f1f1;
  border-color: #dbdbdb;
  color: #999;
  box-shadow: none;
}
.veui-input:disabled,
.veui-textarea:disabled {
  cursor: not-allowed;
}
.veui-input::-webkit-input-placeholder,
.veui-textarea::-webkit-input-placeholder {
  color: #999;
}
.veui-input:-ms-input-placeholder,
.veui-textarea:-ms-input-placeholder {
  color: #999;
}
.veui-input::placeholder,
.veui-textarea::placeholder {
  color: #999;
}
.veui-input {
  padding: 0 11px;
}
.veui-input[ui~="large"] {
  height: 42px;
  font-size: 16px;
}
.veui-input[ui~="small"] {
  height: 30px;
  font-size: 12px;
}
.veui-textarea {
  padding: 6.5px 12px;
  line-height: 1.5;
  resize: none;
}
.veui-textarea.veui-textarea-resizable {
  resize: both;
}



