.veui-demo-calendar section {
  *zoom: 1;
  margin-bottom: 10px;
}
.veui-demo-calendar section:before,
.veui-demo-calendar section:after {
  display: table;
  content: "";
}
.veui-demo-calendar section:after {
  clear: both;
}
.veui-demo-calendar .veui-calendar {
  float: left;
  margin-right: 10px;
}
.veui-demo-calendar .veui-calendar-weekend:not(.veui-calendar-selected) button {
  background-color: #f0ebf5;
}



