/* Metrics */
.veui-calendar {
  display: inline-block;
  position: relative;
  overflow: hidden;
  border: 1px solid #e7e7e7;
  border-radius: 4px;
  background-color: #fff;
}
.veui-calendar::before {
  content: "";
  position: absolute;
  top: 36px;
  left: 0;
  right: 0;
  height: 0;
  border-bottom: 1px solid #e7e7e7;
}
.veui-calendar-panel {
  display: inline-block;
}
.veui-calendar-panel + .veui-calendar-panel {
  margin-left: 10px;
}
.veui-calendar-body {
  border-collapse: collapse;
  table-layout: fixed;
}
.veui-calendar-body button {
  position: relative;
  width: 100%;
  height: 35px;
  margin-top: 1px;
  border: none;
  padding: 0;
  background-color: #fff;
  color: #999;
  text-align: center;
  outline: none;
}
.veui-calendar-body button:hover,
.veui-calendar-body button:focus {
  background-color: #f7f7f7;
}
.veui-calendar-body button:disabled,
.veui-calendar-body button:disabled:hover {
  background-color: #f7f7f7;
  color: #dbdbdb;
  cursor: not-allowed;
}
.veui-calendar th {
  width: 36px;
  height: 34px;
  font-weight: 400;
  color: #999;
}
.veui-calendar td {
  position: relative;
  width: 36px;
  height: 36px;
  padding: 0;
  color: #666;
}
.veui-calendar th,
.veui-calendar td {
  text-align: center;
}
.veui-calendar-days th:first-child,
.veui-calendar-days th:last-child {
  width: 38px;
}
.veui-calendar-aux button {
  color: #dbdbdb;
}
.veui-calendar-today button {
  color: #3685ff;
  font-weight: 600;
}
.veui-calendar-body.veui-calendar-multiple-range .veui-calendar-selected button,
.veui-calendar-in-range button {
  background-color: #d7e7ff;
  border-radius: 0;
  color: #666;
}
.veui-calendar-body.veui-calendar-multiple-range .veui-calendar-selected button:hover,
.veui-calendar-in-range button:hover,
.veui-calendar-body.veui-calendar-multiple-range .veui-calendar-selected button:focus,
.veui-calendar-in-range button:focus {
  background-color: #ebf3ff;
}
.veui-calendar-selected button {
  position: relative;
  color: #fff;
  background-color: #3685ff;
  border-radius: 2px;
}
.veui-calendar-selected button:hover,
.veui-calendar-selected button:focus {
  background-color: #3178e6;
}
.veui-calendar-range-start::after,
.veui-calendar-range-end::before {
  content: "";
  position: absolute;
  top: 1px;
  bottom: 0;
  width: 2px;
  background: #d7e7ff;
  z-index: -1;
}
.veui-calendar-range-start::after {
  right: 0;
}
.veui-calendar-range-end::before {
  left: 0;
}
.veui-calendar-head {
  border-bottom: 1px solid #e7e7e7;
  text-align: center;
  color: #666;
  *zoom: 1;
}
.veui-calendar-head:before,
.veui-calendar-head:after {
  display: table;
  content: "";
}
.veui-calendar-head:after {
  clear: both;
}
.veui-calendar-head button {
  height: 36px;
  background: none;
  border: none;
  padding: 0 7px;
  outline: none;
}
.veui-calendar-head button .veui-icon {
  width: 16px;
  vertical-align: middle;
  color: #999;
}
.veui-calendar-head button:hover,
.veui-calendar-head button:focus {
  color: #3685ff;
}
.veui-calendar-head button:hover .veui-icon,
.veui-calendar-head button:focus .veui-icon {
  color: #3685ff;
}
.veui-calendar-label {
  line-height: 36px;
  color: #3685ff;
}
.veui-calendar-select:hover,
.veui-calendar-select:focus {
  background-color: #ebf3ff;
}
.veui-calendar-prev,
.veui-calendar-next {
  width: 36px;
}
.veui-calendar-prev {
  float: left;
}
.veui-calendar-next {
  float: right;
}
.veui-calendar-months td,
.veui-calendar-years td {
  width: 60px;
  height: 65px;
}
.veui-calendar-months td button,
.veui-calendar-years td button {
  width: 54px;
  height: 54px;
}
.veui-calendar-months .veui-calendar-body,
.veui-calendar-years .veui-calendar-body {
  margin: 5px 8px 14px;
}



