/* Metrics */
.veui-toast {
  position: relative;
  padding: 14px 30px;
  max-width: 800px;
  background-color: #fff;
  border-radius: 2px;
}
.veui-toast span {
  display: inline-block;
}
.veui-toast-message {
  margin-left: 35px;
}
.veui-toast-icon {
  position: absolute;
  top: 50%;
  left: 30px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.veui-toast-success {
  color: #3faca3;
  border: 1px solid rgba(63, 172, 163, 0.5);
  box-shadow: 0 1px 4px rgba(57, 155, 147, 0.2);
}
.veui-toast-warning {
  color: #fe9700;
  border: 1px solid rgba(254, 151, 0, 0.5);
  box-shadow: 0 1px 4px rgba(229, 136, 0, 0.2);
}
.veui-toast-info {
  color: #86b6ff;
  border: 1px solid rgba(134, 182, 255, 0.5);
  box-shadow: 0 1px 4px rgba(54, 133, 255, 0.2);
  color: #3685ff;
}
.veui-toast-error {
  color: #ff5b5b;
  border: 1px solid rgba(255, 91, 91, 0.5);
  box-shadow: 0 1px 4px rgba(230, 82, 82, 0.2);
}



