/* Metrics */
.veui-alert {
  position: relative;
  margin: 30px 0;
  padding: 14px 20px;
  border-radius: 2px;
}
.veui-alert span {
  display: inline-block;
}
.veui-alert-icon {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  position: absolute;
  top: 50%;
  left: 20px;
}
.veui-alert-message {
  margin: 0 44px 0 34px;
}
.veui-alert-message-multiple {
  margin-right: 82px;
}
.veui-alert-success {
  background-color: rgba(63, 172, 163, 0.1);
  color: #3faca3;
}
.veui-alert-warning {
  background-color: rgba(254, 151, 0, 0.1);
  color: #fe9700;
}
.veui-alert-info {
  background-color: #ebf3ff;
  color: #3685ff;
}
.veui-alert-error {
  background-color: rgba(255, 91, 91, 0.1);
  color: #ff5b5b;
}
.veui-alert-close {
  position: absolute;
  top: 50%;
  right: 20px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.veui-alert-close button + button {
  margin-left: 24px;
}
.veui-alert-close.veui-alert-close-text {
  color: #86b6ff;
}
.veui-alert-close.veui-alert-close-text:hover,
.veui-alert-close.veui-alert-close-text:visited {
  color: #72a9ff;
}
.veui-alert-close.veui-alert-close-text:active {
  color: #5e9dff;
}
.veui-alert button {
  border: none;
  background: none;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #666;
}
.veui-alert button:focus {
  outline: none;
}
.veui-alert button:hover,
.veui-alert button:visited {
  color: #3685ff;
}
.veui-alert button:active {
  color: #3178e6;
}
.veui-alert button:disabled {
  cursor: not-allowed;
  color: #999;
}



