.veui-table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
}
.veui-table-cell {
  display: inline-block;
  line-height: 30px;
}
.veui-table th,
.veui-table td {
  height: 54px;
  padding: 12px;
  color: #666;
  text-align: left;
  white-space: nowrap;
}
.veui-table th {
  font-weight: 500;
}
.veui-table td {
  border: 1px solid #e7e7e7;
  border-style: solid none;
}
.veui-table tfoot th {
  border-top: 1px solid #e7e7e7;
}
.veui-table[ui~="slim"] th,
.veui-table[ui~="slim"] td {
  height: 48px;
  padding-top: 9px;
  padding-bottom: 9px;
}
.veui-table[ui~="alt"] thead th {
  color: #999;
}
.veui-table[ui~="alt"] thead,
.veui-table[ui~="alt"] tfoot {
  background-color: #f7f7f7;
}
td.veui-table-no-data {
  text-align: center;
}
.veui-table-selected-row td {
  background-color: #ebf3ff;
}
.veui-table .veui-button + .veui-button {
  margin-left: 30px;
}
.veui-table-header {
  display: inline-block;
  vertical-align: middle;
}
.veui-table-sorter {
  margin-left: 5px;
  vertical-align: middle;
}



