/* Metrics */
.veui-dropdown-options {
  border: 1px solid #e7e7e7;
  margin: 3px 0;
  border-radius: 2px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
}
.veui-dropdown-button {
  width: 100%;
  padding-right: 12px;
  padding-left: 12px;
  text-align: left;
}
.veui-dropdown-label {
  display: inline-block;
  max-width: calc(100% - 28px);
  width: 100%;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.veui-dropdown-icon {
  float: right;
}
.veui-dropdown-expanded .veui-dropdown-button:not(.veui-button-loading),
.veui-dropdown-expanded .veui-dropdown-button:not(.veui-button-loading):hover {
  color: #3685ff;
}
.veui-dropdown-option {
  cursor: pointer;
  height: 36px;
  line-height: 1;
  padding: 11px 10px;
  color: #666;
}
.veui-dropdown-option:hover {
  background-color: #ebf3ff;
}
.veui-dropdown-option-selected {
  color: #3685ff;
}
.veui-dropdown-option-disabled {
  background-color: #fff;
  color: #999;
  cursor: not-allowed;
}
.veui-dropdown-option-label {
  margin-right: 10px;
}
.veui-dropdown {
  display: inline-block;
  width: 110px;
}
.veui-dropdown-options {
  min-width: 110px;
  max-height: 280px;
  overflow-y: auto;
  background-color: #fff;
}
.veui-dropdown[ui~="link"] .veui-dropdown-button {
  padding: 9px 12px;
}



