/* Metrics */
.veui-datepicker-overlay {
  border: 1px solid #e7e7e7;
  margin: 3px 0;
  border-radius: 2px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
}
.veui-datepicker-button {
  width: 100%;
  padding-right: 12px;
  padding-left: 12px;
  text-align: left;
}
.veui-datepicker-label {
  display: inline-block;
  max-width: calc(100% - 28px);
  width: 100%;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.veui-datepicker-icon {
  float: right;
}
.veui-datepicker-expanded .veui-datepicker-button:not(.veui-button-loading),
.veui-datepicker-expanded .veui-datepicker-button:not(.veui-button-loading):hover {
  color: #3685ff;
}
.veui-datepicker {
  position: relative;
  display: inline-block;
  width: 160px;
}
.veui-datepicker-empty .veui-button {
  color: #999;
}
.veui-datepicker-clear {
  display: none;
  position: absolute;
  top: 10px;
  right: 12px;
  width: 16px;
  height: 16px;
  background: #fff;
  outline: none;
  border: none;
  padding: 0;
  color: #999;
  cursor: pointer;
}
.veui-datepicker-clear:hover {
  color: #666;
}
.veui-datepicker-clear:hover,
.veui-button:hover + .veui-datepicker-clear {
  display: block;
}
.veui-datepicker-range {
  width: 300px;
}
.veui-datepicker-range .veui-datepicker-label {
  width: 110px;
}
.veui-datepicker-tilde {
  display: inline-block;
  line-height: 1;
  vertical-align: middle;
  width: 16px;
  margin: 0 12px;
}
.veui-datepicker-shortcuts {
  height: 40px;
  padding: 10px 16px;
  border-top: 1px solid #e7e7e7;
}
.veui-datepicker-shortcut {
  margin-right: 10px;
  border: none;
  color: #666;
  font-size: 12px;
}
.veui-datepicker-shortcut:last-child {
  margin-right: 0;
}



