/* Metrics */
.veui-select-options {
  border: 1px solid #e7e7e7;
  margin: 3px 0;
  border-radius: 2px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
}
.veui-select-button {
  width: 100%;
  padding-right: 12px;
  padding-left: 12px;
  text-align: left;
}
.veui-select-label {
  display: inline-block;
  max-width: calc(100% - 28px);
  width: 100%;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.veui-select-icon {
  float: right;
}
.veui-select-expanded .veui-select-button:not(.veui-button-loading),
.veui-select-expanded .veui-select-button:not(.veui-button-loading):hover {
  color: #3685ff;
}
.veui-select {
  display: inline-block;
  width: 160px;
}
.veui-select-options {
  min-width: 160px;
  max-height: 280px;
  overflow-y: auto;
  background-color: #fff;
}
.veui-select-option-group .veui-select-group-label {
  display: inline-block;
  height: 36px;
  line-height: 36px;
  padding: 0 10px;
  font-size: 12px;
  color: #999;
}
.veui-select-option-group .veui-option {
  padding-left: 20px;
}



