/* Metrics */
.veui-pager {
  color: #666;
  font-weight: normal;
}
.veui-pager a {
  color: inherit;
  text-decoration: none;
  border-radius: 2px;
}
.veui-pager a:focus {
  outline: none;
}
.veui-pager .veui-wrapper {
  *zoom: 1;
}
.veui-pager .veui-wrapper:before,
.veui-pager .veui-wrapper:after {
  display: table;
  content: "";
}
.veui-pager .veui-wrapper:after {
  clear: both;
}
.veui-pager .veui-page-info {
  float: left;
  margin-right: 15px;
  line-height: 30px;
}
.veui-pager .veui-page-size {
  margin-left: 15px;
}
.veui-pager .veui-page-switch {
  float: left;
}
.veui-pager .veui-select {
  width: auto;
  margin-left: 8px;
  vertical-align: text-top;
  line-height: 1;
}
.veui-pager .veui-select .veui-button {
  white-space: nowrap;
  color: #333;
  font-weight: 600;
  padding-top: 0;
  padding-bottom: 0;
}
.veui-pager .veui-select .veui-button .veui-icon {
  float: left;
}
.veui-pager .veui-select .veui-select-label {
  max-width: none;
  width: auto;
  float: left;
  margin-right: 5px;
}
.veui-pager .veui-buttons {
  float: left;
  margin-left: 15px;
}
.veui-pager .veui-button-previous,
.veui-pager .veui-button-next {
  position: relative;
  float: left;
  width: 36px;
  height: 30px;
  line-height: 34px;
  text-align: center;
  background-color: #f1f1f1;
  border-radius: 2px;
}
.veui-pager .veui-pages {
  float: left;
  margin: 0;
  padding: 0;
  list-style: none;
}
.veui-pager .veui-pages li {
  float: left;
  width: 28px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
  border-radius: 2px;
}
.veui-pager .veui-pages li a {
  display: block;
}
.veui-pager .veui-pages li a:hover {
  color: #333;
  font-weight: 600;
}
.veui-pager .veui-pages li a:focus {
  color: #3685ff;
}
.veui-pager .veui-pages .veui-active a {
  color: #333;
  font-weight: 600;
  background-color: #f1f1f1;
}
.veui-pager .veui-pages span {
  color: #999;
}
.veui-pager .veui-page-digit-length-4 li {
  width: 40px;
}
.veui-pager .veui-page-digit-length-3 li {
  width: 34px;
}
.veui-pager .veui-page-switch,
.veui-pager[ui~="slim"] .veui-page-switch {
  padding: 0 51px;
  position: relative;
}
.veui-pager .veui-button-previous,
.veui-pager[ui~="slim"] .veui-button-previous {
  position: absolute;
  left: 0;
}
.veui-pager[ui~="hetero"] .veui-page-switch,
.veui-pager[ui~="full"] .veui-page-switch {
  padding-left: 0;
}
.veui-pager[ui~="hetero"] .veui-button-previous,
.veui-pager[ui~="full"] .veui-button-previous {
  position: static;
  margin-right: 5px;
}
.veui-pager .veui-page-info {
  display: none;
}
.veui-pager[ui~="full"] .veui-page-info {
  display: block;
}
.veui-pager:not([ui~="slim"]) .veui-button-previous,
.veui-pager:not([ui~="slim"]) .veui-button-next {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  transition-property: border-color, background-color, color, box-shadow;
  transition-duration: .2s;
}
.veui-pager:not([ui~="slim"]) .veui-button-previous:focus,
.veui-pager:not([ui~="slim"]) .veui-button-next:focus {
  color: #3685ff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}
.veui-pager:not([ui~="slim"]) .veui-button-previous:hover,
.veui-pager:not([ui~="slim"]) .veui-button-next:hover {
  background-color: #f7f7f7;
  border-color: #f7f7f7;
  color: #333;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}
.veui-pager:not([ui~="slim"]) .veui-button-previous:active,
.veui-pager:not([ui~="slim"]) .veui-button-next:active {
  border-color: #e7e7e7;
  background-color: #e7e7e7;
  box-shadow: none;
}
.veui-pager:not([ui~="slim"]) .veui-button-previous:disabled,
.veui-pager:not([ui~="slim"]) .veui-button-next:disabled,
.veui-pager:not([ui~="slim"]) .veui-button-previous.veui-disabled,
.veui-pager:not([ui~="slim"]) .veui-button-next.veui-disabled {
  background-color: #f1f1f1;
  color: #999;
  cursor: not-allowed;
  box-shadow: none;
}
.veui-pager[ui~="slim"] .veui-page-switch {
  padding: 0 30px;
}
.veui-pager[ui~="slim"] .veui-page-switch li {
  width: 18.66666667px;
  height: 20px;
  line-height: 20px;
  font-size: 13px;
}
.veui-pager[ui~="slim"] .veui-page-switch .veui-active a {
  background: transparent;
}
.veui-pager[ui~="slim"] .veui-button-previous,
.veui-pager[ui~="slim"] .veui-button-next {
  width: 20px;
  height: 20px;
  line-height: 1.5;
  border: 1px solid #dbdbdb;
  border-radius: 50%;
  color: #999;
  background: transparent;
  transition-property: border-color, background-color, color, box-shadow;
  transition-duration: .2s;
}
.veui-pager[ui~="slim"] .veui-button-previous:focus,
.veui-pager[ui~="slim"] .veui-button-next:focus {
  color: #3685ff;
  box-shadow: none;
}
.veui-pager[ui~="slim"] .veui-button-previous:hover,
.veui-pager[ui~="slim"] .veui-button-next:hover {
  color: #333;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}
.veui-pager[ui~="slim"] .veui-button-previous:active,
.veui-pager[ui~="slim"] .veui-button-next:active {
  background-color: #f1f1f1;
  color: #333;
  box-shadow: none;
}
.veui-pager[ui~="slim"] .veui-button-previous.veui-disabled,
.veui-pager[ui~="slim"] .veui-button-next.veui-disabled {
  background-color: #f1f1f1;
  border-color: #f1f1f1;
  color: #999;
  cursor: not-allowed;
  box-shadow: none;
}
.veui-pager[ui~="slim"] .veui-button-previous .veui-icon,
.veui-pager[ui~="slim"] .veui-button-next .veui-icon {
  width: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.veui-pager[ui~="slim"] .veui-page-digit-length-4 li {
  width: 32px;
}
.veui-pager[ui~="slim"] .veui-page-digit-length-3 li {
  width: 27.2px;
}



