.veui-breadcrumb {
  *zoom: 1;
  margin: 0;
  padding: 0;
  list-style: none;
  color: #999;
}
.veui-breadcrumb:before,
.veui-breadcrumb:after {
  display: table;
  content: "";
}
.veui-breadcrumb:after {
  clear: both;
}
.veui-breadcrumb-separator {
  padding: 0 8px;
}
.veui-breadcrumb-separator .veui-icon {
  vertical-align: middle;
}



