/* Metrics */
.veui-option {
  cursor: pointer;
  height: 36px;
  line-height: 1;
  padding: 11px 10px;
  color: #666;
}
.veui-option:hover {
  background-color: #ebf3ff;
}
.veui-option-selected {
  color: #3685ff;
}
.veui-option-disabled {
  background-color: #fff;
  color: #999;
  cursor: not-allowed;
}
.veui-option-label {
  margin-right: 10px;
}
.veui-option-checkmark {
  float: right;
  display: none;
}
[ui~="checkmark"] .veui-option .veui-option-checkmark {
  display: block;
}



