/* Metrics */
.veui-radiobox {
  display: inline-block;
  margin-right: 20px;
  color: #333;
  font-size: 14px;
  line-height: 1;
}
.veui-radiobox input,
.veui-radiobox .veui-icon {
  display: none;
}
.veui-radiobox span {
  vertical-align: middle;
  cursor: pointer;
}
.veui-radiobox-box {
  display: inline-block;
  position: relative;
  background-color: #fff;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  margin-right: 7px;
  font-size: 16px;
  border: 1px solid #dbdbdb;
  box-shadow: inset 0 1px 2px 0 rgba(0, 0, 0, 0.1);
}
.veui-radiobox:hover .veui-radiobox-box {
  border-color: #3685ff;
}
.veui-radiobox :checked + .veui-radiobox-box {
  border-color: #3685ff;
  box-shadow: none;
}
.veui-radiobox :checked + .veui-radiobox-box::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background-color: #3685ff;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0.5);
          transform: translate(-50%, -50%) scale(0.5);
}
.veui-radiobox :disabled,
.veui-radiobox :disabled ~ span {
  cursor: not-allowed;
}
.veui-radiobox :disabled ~ span {
  color: #999;
}
.veui-radiobox :disabled + .veui-radiobox-box {
  border-color: #dbdbdb;
  background-color: #e7e7e7;
  box-shadow: none;
}
.veui-radiobox :disabled + .veui-radiobox-box::before {
  background-color: #999;
}
.veui-radiobox[ui~="small"] {
  font-size: 12px;
  line-height: 1;
}
.veui-radiobox[ui~="small"] .veui-radiobox-box {
  margin-right: 6px;
  font-size: 12px;
  height: 12px;
  width: 12px;
}
.veui-radiobox[ui~="small"] .veui-radiobox-box::before {
  width: 12px;
  height: 12px;
}



