/* Metrics */
.form-row {
  margin-bottom: 10px;
  *zoom: 1;
}
.form-row:before,
.form-row:after {
  display: table;
  content: "";
}
.form-row:after {
  clear: both;
}
.form-key {
  line-height: 42px;
  float: left;
}
.form-value {
  float: left;
}
.form-value .veui-input,
.form-value .veui-textarea {
  width: 280px;
  margin-right: 10px;
}
.form-value .veui-textarea {
  vertical-align: top;
}
.form-value .veui-textarea:nth-child(1) {
  height: auto;
}
.form-value .veui-textarea:nth-child(2) {
  height: 100px;
}
.hidden-tips {
  line-height: 42px;
}



